<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <el-input type="text" v-model="movieTypeName" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
            <el-button type="primary" @click="add" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!---->
      <el-table
        :data="movieTypeList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">
        <el-table-column
          align="center"
          sortable
          label=""
          prop="vf_te_ID">
        </el-table-column>
        <el-table-column
          align="center"
          sortable
          label=""
          prop="vf_te_Name">
        </el-table-column>
        <el-table-column
          align="center"
          sortable
          label=""
          prop="vf_te_ParentID">
        </el-table-column>


        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row.vf_te_ID)">
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="Delete(scope.row.vf_te_ID)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="addDialog">
        <el-form :model="addOptions">

          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="addOptions.data.vf_te_ParentID" placeholder="">
              <el-option
                v-for="item in movieTypeList"
                :key="item.vf_te_ID"
                :label="item.vf_te_ParentID"
                :value="item.vf_te_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addOptions.data.vf_te_Name" placeholder="" ></el-input>
          </el-form-item>
          <!--<el-form-item label=":" :label-width="formLabelWidth">-->
            <!--<el-date-picker-->
              <!--v-model="addOptions.data.sm_at_CreateTime"-->
              <!--type="date"-->
              <!--placeholder="">-->
            <!--</el-date-picker>-->
          <!--</el-form-item>-->
          <!--<el-form-item label=":" :label-width="formLabelWidth">-->
            <!--<el-input v-model="addOptions.data.sm_at_Cost" placeholder="" ></el-input>-->
          <!--</el-form-item>-->
          <!--<el-form-item label=":" :label-width="formLabelWidth">-->
            <!--<el-input v-model="addOptions.data.sm_at_Remark" placeholder="" type="textarea" :rows="4"></el-input>-->
          <!--</el-form-item>-->
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addDialog = false"> </el-button>
          <el-button type="primary" @click="addSubmit"> </el-button>
        </div>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible.sync="updateDialog">

        <el-form :model="updateMovieTypeObj">

          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="updateMovieTypeObj.vf_te_ParentID" placeholder="">
              <el-option
                v-for="item in movieTypeList"
                :key="item.vf_te_ID"
                :label="item.vf_te_ParentID"
                :value="item.vf_te_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateMovieTypeObj.vf_te_Name" placeholder="" ></el-input>
          </el-form-item>
          <!--<el-form-item label=":" :label-width="formLabelWidth">-->
            <!--<el-input v-model="addOptions.data.vf_te_Name" placeholder="" ></el-input>-->
          <!--</el-form-item>-->
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="updateDialog = false"> </el-button>
          <el-button type="primary" @click="updateSubmit"> </el-button>
        </div>
      </el-dialog>
    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        isOff:true,
        formLabelWidth:'120px',
        isLoading:false,
        addDialog:false,
        movieTypeName:'',
        movieDialog:false,
        updateDialog:false,
        total:0,
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "vf_te_Name": "",
            "vf_te_ParentID": "",

          }
        }
      }
    },
    computed: mapGetters([
      'movieTypeList',
      'updateMovieTypeObj'
    ]),
    created(){
      this.initData(this.movieTypeName)
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(this.movieTypeName,num)
      },

      //
      search(){
        this.initData(this.movieTypeName)
      },
      initData(name,page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",
          "vf_te_ID": "",//
          "vf_te_Name": name?name:"",//
          "vf_te_ParentID": "",//
          "page":  page?page:1,//
          "rows": 5//

        };
        this.isLoading = true;
        this.$store.dispatch('initMovieTypeList',options)
          .then(total=>{
            this.total = total;
            this.isLoading = false;
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      add(){
        this.addDialog=true;
        this.$store.commit('setTranstionFalse')
      },
      //
      addSubmit(){
        this.$store.dispatch('AddMovieType',this.addOptions)
          .then(
            (suc)=>{
              this.$notify(
                {
                  message: suc,
                  type:"success"
                }
              );
              this.initData(this. movieTypeName)
            },
            (err)=>{
              this.$notify(
                {
                  message:err,
                  type:'error'
                }
              );
            }
          )
        this.addDialog=false;

      },
      //
      Update(id){
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse');
        this.$store.commit('initUpdateMovieTypeObj',id)
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",
          "data": this.updateMovieTypeObj
        };
        this.$store.dispatch('UpdateMovieType',updateOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.movieTypeName)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
        this.updateDialog = false
      },
      //
      Delete(id){
        console.log(id)
        let deleteOptions = {
          "loginUserID": "huileyou",  //ID
          "loginUserPass": "123",  //
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",  //
          "data": {
            "vf_te_ID":id,//

          }

        };
        this.$store.dispatch('DeleteMovieType',deleteOptions)
          .then((suc)=>{
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData(this.recommendName,1)
          },err=>{
            this.$notify({
              message: err,
              type: 'error'
            });
          });
      }

    },
  }
</script>
<style scoped>

</style>



