<template>
  <div>
    <section id="wrap">
      <h1 class="userClass"></h1>
      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>
          <el-form-item>
            <!--<el-input type="text" v-model="filmName" auto-complete="off" placeholder="" size="small"></el-input>-->
            <el-select v-model="filmName" placeholder="" size="small">
              <el-option
                v-for="item in filmType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>

            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="movieAudit"
        highlight-current-row
        v-loading=""
        style="width: 100%">
        <el-table-column type="expand">

          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.vf_ve_Content.vf_vo_AuthorID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_Time}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_Size}}M</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_Extend}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_FileURL}}</span>
              </el-form-item>

              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_Title}}</span>

              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_ImageURL}}</span>

              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_ve_CreateTime}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_Remark}}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.vf_ve_Content.vf_vo_Introduce}}</span>
              </el-form-item>

            </el-form>
          </template>

        </el-table-column>
        <el-table-column
          label=""
          prop="vf_ve_ID">
        </el-table-column>
        <el-table-column
          label=""
          prop="vf_ve_TypeName">

        </el-table-column>
        <el-table-column
          label=""
          prop="vf_ve_CreateTime">
          <template slot-scope="props">
            <span>{{ props.row.vf_ve_Content.vf_vo_CreateTime}}</span>
            <!--<span> {{approvalOptions.vf_ve_CreateTime}}</span>-->
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              v-show="scope.row.ts_tg_IsPass!=1"
              @click="approval(scope.row.vf_ve_ID)">
            </el-button>

            <el-button
              size="mini"
              type="primary"
              @click="Update(scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div class="block" style="float: right;padding: 10px 0">

        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >

        </el-pagination>
      </div>
    </section>

    <!---->
    <el-dialog title="" :visible.sync="approvalStatusDialog">
      <el-form :model="approvalOptions">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-radio v-model="approvalOptions.IsPass" label="1"></el-radio>
          <el-radio v-model="approvalOptions.IsPass" label="2"></el-radio>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth" v-show="approvalOptions.IsPass!=1">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 6}"
            placeholder=""
            v-model="approvalOptions.failReason">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="approvalStatusDialog = false"> </el-button>
        <el-button type="primary" @click="approvalStatusSubmit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="updateDialog">

      <el-form :model="updateObj">

        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_AuthorID" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_Size" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_Time" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_FileURL" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_Extend" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_ImageURL" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_Title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_CreateTime" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_Remark" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_vo_Introduce" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateObj.data.vf_ve_Content.vf_te_IDs" placeholder=""></el-input>
        </el-form-item>

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="updateDialog = false"> </el-button>
        <el-button type="primary" @click="updateSubmit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        updateObj: {
          "loginUserID": "huileyou",  //ID
          "loginUserPass": "123",  //
          "operateUserID": "",  //
          "operateUserName": "",  //
          "pcName": "",  //
          "data": {
            "vf_ve_ID": "",  //
            "vf_ve_Type": "",  //
            "vf_ve_Content": {  //
              "vf_vo_Time": "",  //
              "vf_vo_Size": "",  //
              "vf_vo_Extend": "",  //
              "vf_vo_FileURL": "",  //
              "vf_vo_AuthorID": "",  //
              "vf_vo_Title": "",  //
              "vf_vo_ImageURL": "",  //
              "vf_vo_CreateTime": "",  //
              "vf_vo_Introduce": "",  //
              "vf_vo_Remark": "",  //
              "vf_te_IDs": ""
            }
          }

        },
        updateDialog: false,
        filmName: "",
        total: 0,
        filmType: [{
          value: "0",
          label: ''
        }, {
          value: "1",
          label: ''
        },
          {
            value: "2",
            label: ''
          }
        ],
        approvalOptions: {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "vf_ve_ID": "",//
          "vf_ve_Type": "",//
          "vf_ve_Content": "",//
          "vf_ve_CreateTime": "",//
          // "goodID": '',//
          "IsPass": '1',

          failReason: '',//
        },
        checkOption: {
          "loginUserID": "huileyou",  //ID
          "loginUserPass": "123",  //
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",  //
          "vf_ve_ID": "",//
          "vf_vo_PasserID": ""//
        },
        deleteOption: {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",
          "data": {
            "vf_ve_ID": "",//
          }

        },
        formLabelWidth: '120px',

        isLoading: false,
        approvalStatusDialog: false,

      }
    },
    computed: mapGetters([
      'movieAudit',
      'updateMovieReviewObj'

    ]),

    created(){
      this.initData(this.filmName)
    },
    methods: {
      //
      handleCurrentChange(num){
        this.initData(this.filmName, num)

      },
      handleSelect(item) {

      },
      initData(name, page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",//
          "operateUserName": "",//
          "pcName": "",
          "vf_ve_ID": "",//
          "vf_ve_Type": name ? name : "",//
          "page": page ? page : 1,//
          "rows": 5//

        }

        this.isLoading = true;
        this.$store.dispatch('initMovieAudit', options)
        .then(data => {
          this.total = data;
          this.isLoading = false;
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      search(){
        this.initData(this.filmName)
      },
      //
      approval(id){

        this.deleteOption.data.vf_ve_ID = id;
        this.checkOption.vf_ve_ID = id;
        this.$store.commit('setTranstionFalse');
        this.approvalStatusDialog = true;

      },
      //
      approvalStatusSubmit(){


        if (this.approvalOptions.IsPass == 1) {
          //
          console.log(this.checkOption)
          this.$store.dispatch('AuditSubmitOk', this.checkOption)
          .then(success => {
            this.$notify({
              message: success,
              type: 'success'
            });
            this.initData(this.id)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
          this.approvalStatusDialog = false;
        } else {

          // 

          this.$store.dispatch('AuditSubmitNo', this.deleteOption)

          .then(success => {
            this.$notify({
              message: success,
              type: 'success'
            });
            this.initData(this.id)
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          });
          this.approvalStatusDialog = false;

        }
        //   this.approvalOptions.goodID = this.sm_ai_ID
      },
      //checkType,
      check(){

        //if(movieAudit.vf_ve_Type){}
      },
      //
      Update(rowData){
        console.log(rowData)
        this.updateObj.data = rowData;
        this.updateDialog = true;
        this.$store.commit('setTranstionFalse', this.updateObj);
        this.$store.commit('initUpdateMovieReviewObj', rowData)
      },
      //
      updateSubmit(){

        this.$store.dispatch('UpdateMovieReview', this.updateObj)
        .then((suc) => {
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.filmName)
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        });
        console.log(2)
        this.updateDialog = false
      },

    },
  }
</script>
<style scoped>

</style>



