<template>
  <div>
    <!---->
    <section id="wrap">
      <h1 class="userClass"></h1>

      <el-col :span="24" class="formSearch">
        <el-form :inline="true">
          <el-form-item>
            <span>:</span>
          </el-form-item>

          <el-form-item>
            <el-input type="text" v-model="storeName" auto-complete="off" placeholder="" size="small"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="search" size="small"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table
        :data="initFoodReviewStoreInformationList"
        highlight-current-row
        v-loading="isLoading"
        style="width: 100%">

        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">

              <el-form-item label=":">
                <span>{{ props.row.fd_sf_ID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_TypeID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_py_Name }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_MansID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_ProductName }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_Address }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_Lng }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_Lat }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_Provice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_City }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_AvgPrice }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_OpenTime }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_Phone }}</span>
              </el-form-item>
              <el-form-item label="WAFI:">
                <span>{{ props.row.fd_sf_HasWafi }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_TradeID }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_TransInfo }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_Introduce }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.fd_sf_PassStatus }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>

        <el-table-column
          align="center"
          label=""
          prop="fd_sf_ID">
        </el-table-column>
        <el-table-column
          align="center"
          label=""
          prop="fd_sf_ProductName">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="apply(scope.row.fd_sf_ID)">{{scope.row.fd_sf_PassStatus}}
            </el-button>
          </template>
        </el-table-column>

      </el-table>


      <!---->
      <div class="block" style="float: right;">
        <el-pagination
          @current-change="handleCurrentChange"
          :page-size="10"
          layout="total, prev, pager, next"
          :total="total"
          v-show="total"
        >
        </el-pagination>
      </div>

      <!---->

      <el-dialog title="" :visible.sync="applyDialog">
        <el-form :model="applyOptions">

          <el-form-item label=":" :label-width="formLabelWidth">
            <el-select v-model="applyOptions.fd_sf_PassStatus" placeholder="" @change="changeLoes">
              <el-option
                v-for="item in applyState"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" :label-width="formLabelWidth" v-show="showLose">
            <el-input v-model="applyOptions.fd_sf_FailedReason"></el-input>
          </el-form-item>

        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="applyDialog = false"> </el-button>
          <el-button type="primary" @click="applySubmit"> </el-button>
        </div>
      </el-dialog>

    </section>
  </div>
</template>
<script>
  import {mapGetters} from 'vuex'

  export default {
    name: '',
    data() {
      return {
        addDialog: false,
        storeName: '',
        total: 0,
        isLoading: false,
        updateDialog: false,
        formLabelWidth: '120px',
        applyState: [
          {
            value: 0,
            label: ''
          },
          {
            value: 1,
            label: ''
          },
          {
            value: 2,
            label: ''
          },
        ],
        applyOptions: {
          "fd_sf_ID": "",//
          "fd_sf_PassStatus": "",//  0 1  2
          "fd_sf_FailedReason": ""//
        },
        applyDialog: false,
        showLose: false,
      }
    },
    computed: mapGetters([
      'initFoodReviewStoreInformationList',
    ]),
    created() {
      this.initData(name, 1)
    },
    methods: {
      //
      changeLoes(id) {
        if (id == 2) {
          this.showLose = true;
        } else {
          this.showLose = false;
        }
      },
      //
      handleCurrentChange(num) {
        this.initData(this.storeName, num)
      },
      //
      initData(name, page) {
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "fd_sf_ProductName": name ? name : "",// like
          "page": page ? page : "1",
          "rows": "10",
        };
        this.isLoading = true;
        this.$store.dispatch('initFoodReviewStoreInformation', options)
          .then(total => {
            this.total = total;
            this.isLoading = false;
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
      },
      //
      search() {
        this.initData(this.storeName, "1")
      },
      //
      apply(id) {
        this.$store.commit('setTranstionFalse');
        this.applyDialog = true;
        this.applyOptions.fd_sf_ID = id;
      },
      applySubmit() {
        let validateStoreFront = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "1",
          "operateUserName": "",
          "pcName": "",
          "fd_sf_ID": this.applyOptions.fd_sf_ID,//
          "fd_sf_PassStatus": this.applyOptions.fd_sf_PassStatus,//  0 1  2
          "fd_sf_FailedReason": this.applyOptions.fd_sf_FailedReason//
        }
        this.$store.dispatch('applyFoodReviewStoreInformation',validateStoreFront)
          .then(suc => {
            this.$notify({
              message: suc,
              type: 'success'
            });
            this.initData()
          }, err => {
            this.$notify({
              message: err,
              type: 'error'
            });
          })
        this.applyDialog = false;
      }
    },
  }
</script>
<style scoped>

</style>





