<template>
  <section id="wrap">
    <h1 class="userClass"></h1>
    <el-col :span="24" class="formSearch">
      <el-form :inline="true">
        <el-form-item>
          <span>:</span>
        </el-form-item>
        <el-form-item>
          <!--<el-input type="text" v-model="carName" auto-complete="off" placeholder="" size="small"></el-input>-->
          <el-select v-model="typeID" placeholder="">
            <el-option
              v-for="item in carCityLandmarksBasisTypeList"
              :key="item.cr_cra_Id"
              :label="item.cr_cra_Name"
              :value="item.cr_cra_Id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search" size="small"></el-button>
          <el-button type="primary" @click="Add" size="small"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="carCityLandmarksList"
      highlight-current-row
      v-loading="isLoading"
      style="width: 100%">
      <el-table-column
        align="center"
        label="ID"
        prop="cr_cl_Id">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="cr_cl_CityName">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="cr_cl_Name">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="cr_cl_AttrName">
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="Update(scope.row)">
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="Delete(scope.row.cr_cl_Id)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <div class="block" style="float: right;">
      <el-pagination
        @current-change="handleCurrentChange"
        :page-size="10"
        layout="total, prev, pager, next"
        :total="total"
        v-show="total"
      >
      </el-pagination>
    </div>

    <!---->
    <el-dialog title="" :visible.sync="addDialog">
      <el-form :model="addOptions">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="proviceID" placeholder="" size="mini" @change="changeProvice">
            <el-option
              v-for="item in carProvinceDataList"
              :key="item.sm_af_AreaID"
              :label="item.sm_af_AreaName"
              :value="item.sm_af_AreaID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="addOptions.data.cr_cl_CityId" placeholder="" size="mini">
            <el-option
              v-for="item in carCityDataList"
              :key="item.sm_af_AreaID"
              :label="item.sm_af_AreaName"
              :value="item.sm_af_AreaID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="addOptions.data.cr_cl_AttrId" placeholder="" size="mini">
            <el-option
              v-for="item in carCityLandmarksBasisTypeList"
              :key="item.cr_cra_Id"
              :label="item.cr_cra_Name"
              :value="item.cr_cra_Id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="addOptions.data.cr_cl_Name" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="addSubmit"> </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="updateDialog">
      <el-form :model="updateOptions">
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="proviceID" placeholder="" size="mini" @change="changeProvice">
            <el-option
              v-for="item in carProvinceDataList"
              :key="item.sm_af_AreaID"
              :label="item.sm_af_AreaName"
              :value="item.sm_af_AreaID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateOptions.cr_cl_CityId" placeholder="" size="mini">
            <el-option
              v-for="item in carCityDataList"
              :key="item.sm_af_AreaID"
              :label="item.sm_af_AreaName"
              :value="item.sm_af_AreaID">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-select v-model="updateOptions.cr_cl_AttrId" placeholder="" size="mini">
            <el-option
              v-for="item in carCityLandmarksBasisTypeList"
              :key="item.cr_cra_Id"
              :label="item.cr_cra_Name"
              :value="item.cr_cra_Id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" :label-width="formLabelWidth">
          <el-input v-model="updateOptions.cr_cl_Name" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="updateSubmit"> </el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
  import {mapGetters} from 'vuex'
  export default{
    name: '',
    data(){
      return {
        typeID: '',
        total: 0,
        proviceID:'',
        isLoading: false,
        addDialog: false,
        updateDialog: false,
        formLabelWidth: '120px',
        updateOptions: {},
        addOptions:{
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "cr_cl_CityId": "",//ID
            "cr_cl_Name": "",//
            "cr_cl_AttrId": "",//Id
          }
        }
      }
    },
    computed: mapGetters([
      'carCityLandmarksBasisTypeList',
      'carCityLandmarksList',
      'carProvinceDataList',
      'carCityDataList'
    ]),
    created(){
      this.initCarBasis().then(() => {
        this.initData('', 1)
      }, err => {
        console.log(err)
      })
    },
    methods: {
      //
      search(){
        this.initData(this.typeID, 1);
      },
      //
      handleCurrentChange(num){
        this.initData(this.typeID, num);
      },
      //
      async initCarBasis(){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "page": "",//
          "rows": "",//
          "cr_cra_Id": "",//
          "cr_cra_Name": "",//
          "cr_car_PId": "6",//ID
        };
        await this.$store.dispatch('initCarCityLandmarksBasisType', options)

        let cityOptions = {
          areaPid: 3337
        };
        await this.$store.dispatch('initCarProvinceData', cityOptions)
      },
      //
      changeProvice(){
        let options = {
          areaPid:this.proviceID
        }
        this.$store.dispatch('initCarCityData', options)
      },
      //
      cancel(){
        this.initData('', 1);
        this.addDialog = false;
        this.updateDialog = false;
      },
      initData(typeID, page){
        let options = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "page": page ? page : 1,//
          "rows": "10",//
          "cr_cl_Id": "",//ID
          "cr_cl_CityId": "",//ID
          "cr_cl_AttrId": typeID ? typeID : '',//Id
        };
        this.isLoading = true;
        this.$store.dispatch('initCarCityLandmarks', options)
        .then(total => {
          this.total = total;
          this.isLoading = false;
        }, err => {
          this.$notify({
            message: err,
            type: 'error'
          });
        })
      },
      //
      Add(){
        this.$store.commit('setTranstionFalse');
        this.addDialog = true;
      },
      //
      addSubmit(){
        this.$store.dispatch('AddCarCityLandmarks',this.addOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.typeID,1)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        });
        this.addDialog = false;
      },
      //
      Update(obj){
        this.updateOptions = obj;
        this.$store.commit('setTranstionFalse');
        this.updateDialog = true;
      },
      //
      updateSubmit(){
        let updateOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "cr_cl_Id": this.updateOptions.cr_cl_Id,//ID
            "cr_cl_CityId": this.updateOptions.cr_cl_CityId,//ID
            "cr_cl_Name": this.updateOptions.cr_cl_Name,//
            "cr_cl_AttrId": this.updateOptions.cr_cl_AttrId,//Id
          }
        };
        this.$store.dispatch('UpdateCarCityLandmarks',updateOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData(this.typeID,1)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        });
        this.updateDialog = false;
      },
      //
      Delete(id){
        let deleteOptions = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "operateUserID": "",
          "operateUserName": "",
          "pcName": "",
          "data": {
            "cr_cl_Id": id//ID
          }
        };
        this.$store.dispatch('DeleteCarCityLandmarks',deleteOptions)
        .then((suc)=>{
          this.$notify({
            message: suc,
            type: 'success'
          });
          this.initData('',1)
        },err=>{
          this.$notify({
            message: err,
            type: 'error'
          });
        });
      }
    },
  }
</script>
<style scoped>

</style>



